.our_Recipe_bg {
    background-image: url("https://img.freepik.com/free-photo/chicken-wings-barbecue-sweetly-sour-sauce-picnic-summer-menu-tasty-food-top-view-flat-lay_2829-6470.jpg?w=2000");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-size: fixed;
    width: 100%;
    height: 80vh;
}
.Recipes_bg{
  background-image: url(../../Image/backgroundIconImage.jpg);
}
.our_meat_text {

    text-shadow: 6px 4px black;
    font-size: 40px;
    padding: 15px;
    color: white;
    padding-top: 50px;
    font-weight: 900;

}

.Recipes_img img {

    margin-top: -3%;
    margin-bottom: -9%;
    width: 40%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;

}

@media (max-width: 414px) {
    .Recipes_img img {
        padding-top: 110px;
        width: 70%;
        margin-bottom: 30px;
    }
}

.Conatainer_Recipes{
    justify-content: center;
    gap: 35px;
}

.container_Recipes {
    position: relative;
    font-family: Arial;
  }
  
  .text-block_Recipes {
    position: absolute;
    bottom: 0.1px;
    /* right: 20px; */
    background-color:rgba(25,25,25,0.9);
    color: white;
    /* padding-left: 10px; */
    /* padding-right: 20px; */
    width: 100%;
    transition: 1s;
  }
  .text-block_Recipes:hover{
    transform: translateY(-8px);

  }
  .text-block_Recipes p{
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    font-weight: 600;
    padding-top: 13px;

  }
  .container_Recipes img{
    width: 100%;
    height: 30vh;

  }

  .fatima_modals_img img{
    width: 10%;
    height: auto;
    
  }

  .our_recipes_102{
    position:absolute;
    top:50%;
    left: 50%;
    right: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    height:100%;
    overflow:scroll;
    outline:none;
    
  }


  @media(max-width:500px){
    .our_recipes_102{
      position:absolute;
      top:70%;
      left: 50%;
      right: 50%;
      transform: translate(-50%, -50%);
      width: 80%;
      height:100%;
      /* overflow:scroll; */
      outline:none;
    }
  }

  @media ((max-width:1200px) and (min-width:510px)){
     .our_recipes_102{
      position:absolute;
      top:62%;
      left: 50%;
      right: 50%;
      transform: translate(-50%, -50%);
      width: 50%;
      height:100%;
      outline:none;
     }
  }